<template>
  <div class="main">
    <el-table
      :data="books"
      stripe
      :default-sort="{ prop: 'time', order: 'descending' }"
    >
      <el-table-column
        prop="name"
        label="书名"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="author"
        label="作者"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="time"
        label="写作年份"
        sortable
        align="center"
      ></el-table-column>
      <el-table-column prop="rate" label="个人评价" sortable align="center">
        <template slot-scope="scope">
          <el-rate :value="scope.row.rate / 2" disabled />
        </template>
      </el-table-column>
      <el-table-column label="标签" align="center">
        <template slot-scope="scope">
          <div v-if="scope.row.tag !== ''">
            <el-tag v-for="tag in scope.row.tag.split('/')" :key="tag">
              {{ tag }}
            </el-tag>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import books from "@/assets/books";
export default {
  data() {
    return {
      books,
    };
  },
  created() {
    this.$notify.info({
      title: "消息",
      message: "优书网无了，个人记录下看过的小说和评价。",
    });
    this.$notify.info({
      title: "提示",
      message: "国产网文与日本轻小说评价标准并不完全相同",
      offset: 100,
      duration: 9000,
    });
  },
};
</script>

<style lang="less" scoped>
.el-tag {
  margin: 0.3vh 0.3vw;
}
</style>